import React, { useState } from "react";
import { DatePicker } from "antd";
import "./style.scss";

const calendar = require("@assets/images/calender.svg");
interface IPropsFloatDatePicker {
  label?: string;
  value?: any;
  placeholder?: string;
  type?: string;
  required?: boolean;
  onChange?: () => void;
  name?: string;
}

const FloatDatePicker = (props: IPropsFloatDatePicker) => {
  const [focus, setFocus] = useState(false);
  let { label, value, placeholder, type, required } = props;

  if (!placeholder) placeholder = label;

  const isOccupied = focus || (value && value.length !== 0);

  const labelAsLabel = "top-[-8px] py-[4px] ml-[-4px]";
  const labelAsPlaceHolder = "text-red-100";
  const labelClass = isOccupied ? labelAsLabel : labelAsPlaceHolder;
  const dateFormat = "DD/MM/YYYY";
  const requiredMark = required ? <span className="text-danger">*</span> : null;

  return (
    <div
      className="float-label relative bg-white-100"
      onBlur={() => setFocus(false)}
      onFocus={() => setFocus(true)}
    >
      <DatePicker
        onChange={props.onChange}
        defaultValue={value}
        className="date-picker-antd"
        placeholder=""
        suffixIcon={<img src={calendar} alt="calendar" />}
        format={dateFormat}
      />
      <label
        className={
          "top-[7px] ml-[-4px] left-[16px] absolute t-12-semibold text-gray-600 z-10"
        }
      >
        {label} {requiredMark}
      </label>
      {!isOccupied && (
        <label
          className={
            "top-[26px] ml-[-4px] left-[16px] absolute t-14-normal text-gray-400 z-10"
          }
        >
          {placeholder}
        </label>
      )}
    </div>
  );
};

export default FloatDatePicker;
